
<template>
  <!-- <div>
      home页
      <h1>我是home页的h1</h1>
  </div>-->
  <el-container>
    <el-header>
      <el-row>
        <el-col :span="8" class="col_l">
          <img src="../../assets/images/logo.png" alt width="100px" />
        </el-col>
        <el-col :span="8">
          <h1>电商管理系统</h1>
        </el-col>
        <el-col :span="8" class="col_r">
          恭喜发财，好运来！
          <a @click.prevent="logout">退出</a>
        </el-col>
      </el-row>
    </el-header>
    <el-container>
      <el-aside width="200px">
        <!-- 左侧栏 -->
        <!-- el-menu：菜单组件
            el-submenu:菜单子组件（可展开）
            el-menu-item 菜单元素（最小单位）
            default-active="2"默认选中高亮的
            @open="handleOpen"
            @close="handleClose"
        -->
        <el-menu
          :router="true"
          :default-active="$route.path"
          class="el-menu-vertical-demo"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
        >
          <el-submenu :index="item.id+''" v-for="item in menus" :key="item.id">
            <!-- 标题 -->
            <template slot="title">
              <i class="el-icon-location"></i>
              <span >{{item.authName}}</span>
            </template>
            <el-menu-item :index="'/'+item1.path" v-for="item1 in item.children" :key="item1.id">
              {{item1.authName}}
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main class='el-main'>
        <!-- 子组件的出口 -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
/* eslint-disable */
export default {
  data(){
    return {
      menus:[]
    }
  },
  created() {
    this.getMenu();
  },
  methods: {
    async logout() {
      try {
        // 确认框
        const p = await this.$confirm(
          "此操作将永久退出该账户, 是否继续?",
          "提示",
          {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
            // 点击确定
          }
        );
        console.log("点击了确定");
        // 1. 清除token令牌
        localStorage.removeItem("token");
        // 2. 提示退出成功
        this.$message({
          message: "退出成功",
          type: "success",
          duration: 800
        });
        // 3. 跳转到login页面
        this.$router.push("/login");
      } catch (error) {
        console.log(error);
        this.$message({
          message: "取消退出",
          type: "info",
          duration: 800
        });
      }

      // .then(() => {
      console.log("confirm");
      // 1. 清除token令牌
      localStorage.removeItem("token");
      // 2. 提示退出成功
      this.$message({
        message: "退出成功",
        type: "success",
        duration: 800
      });
      // 3. 跳转到login页面
      this.$router.push("/login");
      //  点击取消
      // })
      // .catch(() => {
      //   console.log("cancel");
      // this.$message({
      //   message: "取消退出",
      //   type: "info",
      //   duration: 800
      // });
      // });
    },
    async getMenu(){
      let res=await this.$axios.get("menus");
      this.menus=res.data.data;
      // console.log(this.menus)
    }
  }
};
</script>
<style lang="" scoped>
/* 头部 */
.el-header {
  background-color: #b3c1cd;
  padding: 0;
}
.col_l {
  height: 60px;
  padding-left: 30px;
  line-height: 60px;
  padding-top: 10px;
}
.col_l img {
  line-height: 60px;
}
h1 {
  color: #fff;
  text-align: center;
  line-height: 60px;
  font-size: 26px;
}
.col_r {
  text-align: center;
  line-height: 60px;
  padding-right: 30px;
}
.col_r a {
  color: green;
}
/* 侧栏 */
.el-aside {
  background-color: #545c64;
}
/* 主体 */
.el-main {
  background-color: #ccc;
}
/* 外部容器 */
.el-container {
  height: 100%;
  min-width: 900px;
}
/* 右边容器 */
.el-main{
  background-color: #ccc;
}
</style>



